*, *:before, *:after {
    box-sizing: border-box;
}

::selection {
    background-color: red;
    color: gold;
}

html, body {
    height: 100%;
}

body {
    margin: 0;
}

.body-content {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.scroll-content {
    flex-grow: 1;
}


header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
















.current {
    background-color: gold;
}













.dev-only {
    .info-layout {
        position: fixed;
        left: 0.5rem;
        bottom: 1.5rem;
        padding: 0.25em;
        background-color: #e9e9e9;
        font-size: 0.875rem;
    }
}
